<style lang="less">

</style>
<template>
  <div>
    <Card style="min-height:300px;" :dis-hover="true" :shadow="false" :bordered="false">
      <div slot="title">
        <h3>Sql Review</h3>
      </div>
      <editor v-model="form.sql_text" @init="editorInit" @setCompletions="setCompletions"></editor>
      <br>
      <Button type="primary" @click="createReview" :loading="loading" :disabled="form.sql_text === ''">
        
      </Button>
    </Card>
  </div>
</template>
<script>
    import axios from 'axios'
    import editor from '@/components/editor'
    import util from '@/libs/util'
    import { addSingleReview } from '@/api/task'

    export default {
        components: {
          editor: editor,
        },
        name: 'SingleReview',
        props: {
        },
        data() {
            return {
              form: {
                sql_text: "",
                tenant: sessionStorage.getItem('tenant_name'),
                task_type: 'SINGLE'
              },
              wordList: [],
              loading: false
            }
        },
        methods: {
          createTask() {
            this.loading = true
            addSingleReview(this.form).then(res => {
              if (res.data.status === 'success') {
                this.$Message.success({
                  content: 'sqlReview ',
                  duration: 5,
                  closable: true
                });
                util.openPage(this, 'task_list')
              } else {
                this.$Message.error({
                  content: 'sqlReview schema',
                  duration: 5,
                  closable: true
                });
              }
            }).catch(e => {
              this.bus.$emit('errorHandler', e);
            }).finally(() => {
              this.loading = false
            })
          },
          setCompletions(editor, session, pos, prefix, callback) {
            callback(null, this.wordList.map(function (word) {
              return {
                caption: word.vl,
                value: word.vl,
                meta: word.meta
              }
            }))
          },
          editorInit: function () {
            require('brace/mode/mysql')
            require('brace/theme/xcode')
          },
          createReview() {
            this.$Modal.confirm({
              title: 'Review',
              'z-index': 1010,
              // content: 'Review',
              onOk: () => {
                this.createTask()
              },
              onCancel: () => {
              }
            });
          }
        },
        mounted() {
        }
    }
</script>
