<style lang="less">

</style>
<template>
  <div>
    <Card style="min-height:300px;" :dis-hover="true" :shadow="false" :bordered="false">
      <div slot="title">
        <h3>Sql Review</h3>
      </div>
      <Row>
        <Col :span="12">
          <Form :model="form" ref="createForm" :rules="formValidate">
            <FormItem label=":" prop="app">
              <Select v-model="form.app" label="label" filterable  :loading="selectLoading"
                        :remote-method="getData" @on-change="onSelectGit" remote clearable style="min-width:400px">
                <Avatar icon="ios-git-branch" style="color:gray;background-color: transparent" slot="prefix"/>
                <Option v-for="item in gitList" :value="item.value" :key="item.value">: {{ item.label }} - {{item.value }}</Option>
              </Select>
            </FormItem>
            <FormItem label="TAG:" prop="tag">
              <Select :max-tag-count="10" v-model="form.tag" remote :loading="tagLoading" filterable
                        placeholder="" clearable style="min-width:400px">
                <Avatar icon="ios-pricetag-outline" style="color:gray;background-color: transparent" slot="prefix"/>
                <Option v-for="item in tagList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem label=":" prop="base" v-if="isTest">
              <Select :max-tag-count="10" v-model="form.valid" placeholder="" clearable style="min-width:400px">
                <Avatar icon="md-construct" style="color:gray;background-color: transparent" slot="prefix"/>
                <Option v-for="item in validList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
          </Form>
        </Col>
      </Row>
      <br>

      <Button type="primary" size="large" @click="createReview" :disabled="!form.app || !form.tag">
        
      </Button>
    </Card>
  </div>
</template>
<script>
  import {getTaskList} from '@/api/task';
  import util from '@/libs/util'
  import Mock from 'mockjs'
  import {getGitList, getGitTagList} from '@/api/settings'
  import {addGitReview} from '@/api/task'

  export default {
    components: {},
    name: 'GitReview',
    props: {},
    data() {

      const validTag = (rule, value, callback) => {
        if (value === this.form.tag) {
          callback(new Error('tag'))
        } else {
          callback()
        }
      };

      return {
        singleText: '',
        wordList: [],
        form: {
          app: '',
          tag: '',
          valid: '1'
        },
        formValidate: {
          app: [
            {
              required: true,
              message: '',
              trigger: 'blur'
            }
          ],
          tag: [
            {
              required: true,
              message: 'tag',
              trigger: 'blur'
            }
          ],
          // base: [
          //   {
          //     validator: validTag,
          //     trigger: 'blur'
          //   }
          // ],
        },
        query: {
          app_name: ''
        },
        gitList: [],
        tagList: [],
        baseTagList: [],
        validList: [{value: '0', label: ''}, {value: '1', label: ''}],
        selectLoading: false,
        tagLoading: false,
        tagEnum: {}
      }
    },
    computed: {
      isTest() {
        return sessionStorage.getItem('auth').indexOf('admin') >= 0
      }
    },
    methods: {
      async onSelectGit(v) {
        console.log(v)
        console.log(this.tagEnum[v])
        if (!v) {return}
        this.tagList = []
        this.tagLoading = true
        await getGitTagList(this.tagEnum[v]).then(res => {
          console.log(res)
          res.data.data.forEach(v => this.tagList.push({value: v, label: v}))
        }).catch(e => {
          console.log(e)
        })

        this.baseTagList = []
        // await getTaskList(50, 0, 'GITREPOSITORY', '', '', '', this.tagEnum[v], 'SUCCESS').then(res => {
        //   res.data.data.forEach(v => this.baseTagList.push({value: v.current_tag, label: v.current_tag}))
        // }).catch(e => {
        //   console.log(e)
        // })
        this.tagLoading = false
      },
      setCompletions(editor, session, pos, prefix, callback) {
        callback(null, this.wordList.map(function (word) {
          return {
            caption: word.vl,
            value: word.vl,
            meta: word.meta
          }
        }))
      },
      getData(v) {
        this.selectLoading = true
        getGitList(20, 0, '', v).then(res => {
          console.log(res)
          this.gitList = []
          res.data.data.forEach(v => {
            this.tagEnum[v.app_name] = v.repo_url
            this.gitList.push({label: v.repo_url.trimRight(), value: v.app_name.trimRight()})
          })
        }).catch(e => {
          console.log(e)
          this.bus.$emit('errorHandler', e);
        }).finally(() => {
          this.selectLoading = false
        })
      },
      createReview() {
        this.$refs['createForm'].validate((valid) => {
          if (valid) {
            this.$Modal.confirm({
              title: 'Review',
              'z-index': 1010,
              // content: 'Review',
              onOk: () => {
                this.loading = true
                addGitReview(this.form).then(res => {
                  if (res.data.status === 'success') {
                    this.$Message.success({
                      content: 'sqlReview ',
                      duration: 5,
                      closable: true
                    });
                    util.openPage(this, 'task_list')
                  } else {
                    this.$Message.error({
                      content: 'sqlReview ',
                      duration: 5,
                      closable: true
                    });
                  }
                }).catch(e => {
                  this.bus.$emit('errorHandler', e);
                }).finally(() => {
                  this.loading = false
                })
              },
              onCancel: () => {
                // this.$Message.info('Clicked cancel');
              }
            });
          }
        })
      }
    },
    mounted() {
    }
  }
</script>
